"use client";

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import DotGrid from './DotGrid';

interface AIWorkflowsPageProps {
  onClose?: () => void;
}

interface Component {
  id: string;
  name: string;
  category: string;
  icon: string;
}

const componentCategories = [
  {
    name: 'Base',
    components: [
      { id: 'web-search', name: 'Web Search', icon: 'üîç' },
      { id: 'web-scrape', name: 'Web Scrape', icon: 'üåê' },
      { id: 'text-input', name: 'Text Input', icon: 'üìù' },
      { id: 'number-input', name: 'Number Input', icon: 'üî¢' }
    ]
  },
  {
    name: 'Advanced',
    components: [
      { id: 'llm', name: 'LLM', icon: 'ü§ñ' },
      { id: 'content-cleanup', name: 'Content Cleanup', icon: '‚ú®' },
      { id: 'summarize', name: 'Comprehensive Summary', icon: 'üìÑ' },
      { id: 'seo-article', name: 'SEO Article Outline', icon: 'üì∞' }
    ]
  },
  {
    name: 'Tools',
    components: [
      { id: 'json-parser', name: 'JSON Parser', icon: '{ }' },
      { id: 'text-splitter', name: 'Text Splitter', icon: '‚úÇÔ∏è' },
      { id: 'data-merge', name: 'Data Merge', icon: 'üîó' }
    ]
  },
  {
    name: 'Logic',
    components: [
      { id: 'if-else', name: 'If/Else', icon: 'üîÄ' },
      { id: 'loop', name: 'Loop', icon: 'üîÑ' },
      { id: 'filter', name: 'Filter', icon: 'üîç' }
    ]
  },
  {
    name: 'OpenAPI',
    components: []
  },
  {
    name: 'RAG Data',
    components: [
      { id: 'vector-store', name: 'Vector Store', icon: 'üìä' },
      { id: 'embeddings', name: 'Embeddings', icon: 'üßÆ' }
    ]
  }
];

export default function AIWorkflowsPage({ onClose }: AIWorkflowsPageProps) {
  const router = useRouter();
  const [selectedCategory, setSelectedCategory] = useState('Base');
  const [expandedCategories, setExpandedCategories] = useState<string[]>(['Base', 'Advanced']);
  const [searchQuery, setSearchQuery] = useState('');

  const handleClose = () => {
    if (onClose) {
      onClose();
    } else {
      router.push('/command-hub');
    }
  };

  const toggleCategory = (categoryName: string) => {
    setExpandedCategories(prev =>
      prev.includes(categoryName)
        ? prev.filter(c => c !== categoryName)
        : [...prev, categoryName]
    );
  };

  return (
    <div className="workflow-builder">
      {/* Close Button - Top Right */}
      <button className="workflow-close-btn" onClick={handleClose}>‚úï</button>

      {/* Left Sidebar - Components Panel */}
      <aside className="workflow-sidebar">
        <div className="workflow-sidebar-header">
          <div className="workflow-logo">
            <span className="workflow-logo-icon">‚ö°</span>
            <span className="workflow-logo-text">Components</span>
          </div>
        </div>

        <div className="workflow-search-container">
          <input
            type="text"
            className="workflow-search"
            placeholder="Search components"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          <span className="workflow-search-icon">üîç</span>
        </div>

        <nav className="workflow-categories">
          {componentCategories.map((category) => (
            <div key={category.name} className="workflow-category">
              <button
                className={`workflow-category-header ${expandedCategories.includes(category.name) ? 'expanded' : ''}`}
                onClick={() => toggleCategory(category.name)}
              >
                <span className="workflow-category-name">{category.name}</span>
                <span className="workflow-category-arrow">‚Ä∫</span>
              </button>
              {expandedCategories.includes(category.name) && (
                <div className="workflow-category-items">
                  {category.components.length > 0 ? (
                    category.components
                      .filter(comp =>
                        comp.name.toLowerCase().includes(searchQuery.toLowerCase())
                      )
                      .map((component) => (
                        <button key={component.id} className="workflow-component-item">
                          <span className="workflow-component-icon">{component.icon}</span>
                          <span className="workflow-component-name">{component.name}</span>
                        </button>
                      ))
                  ) : (
                    <div className="workflow-empty-category">
                      <button className="workflow-add-button">+</button>
                    </div>
                  )}
                </div>
              )}
            </div>
          ))}
        </nav>
      </aside>

      {/* Main Canvas Area */}
      <main className="workflow-canvas">
        <header className="workflow-header">
          <div className="workflow-header-left">
            <span className="workflow-auto-save">
              <span className="workflow-save-icon">‚òÅÔ∏è</span>
              Auto-Saved 15:32:28
            </span>
          </div>
          <div className="workflow-header-right">
            <button className="workflow-btn workflow-btn-secondary">
              <span className="workflow-btn-icon">üêõ</span>
              Debug Off
            </button>
            <button className="workflow-btn workflow-btn-secondary">
              <span className="workflow-btn-icon">‚ñ∂Ô∏è</span>
              Test
            </button>
            <button className="workflow-btn workflow-btn-primary">Upgrade</button>
            <button className="workflow-btn workflow-btn-secondary">
              <span className="workflow-btn-icon">üöÄ</span>
              Deploy
            </button>
            <button className="workflow-btn workflow-btn-secondary">
              <span className="workflow-btn-icon">‚ÜóÔ∏è</span>
              Share
            </button>
          </div>
        </header>

        <div className="workflow-canvas-content">
          <DotGrid 
            dotSize={2}
            gap={40}
            baseColor="rgba(192, 192, 192, 0.1)"
            activeColor="#c0c0c0"
            proximity={120}
            speedTrigger={80}
            shockRadius={200}
            shockStrength={4}
            className="workflow-dot-grid-bg"
          />
          <div className="workflow-placeholder">
            <div className="workflow-placeholder-icon">‚ö°</div>
            <h2 className="workflow-placeholder-title">Build Your Workflow</h2>
            <p className="workflow-placeholder-text">
              Drag and drop components from the sidebar to create powerful AI workflows
            </p>
            <div className="workflow-placeholder-actions">
              <button className="workflow-placeholder-btn">
                <span className="workflow-placeholder-btn-icon">üìã</span>
                Start from Template
              </button>
              <button className="workflow-placeholder-btn">
                <span className="workflow-placeholder-btn-icon">‚ú®</span>
                Create from Scratch
              </button>
            </div>
          </div>
        </div>

        <footer className="workflow-footer">
          <div className="workflow-footer-actions">
            <button className="workflow-footer-btn" title="Collapse">
              <span className="workflow-footer-icon">‚úï</span>
              Collapse
            </button>
            <button className="workflow-footer-btn" title="Prettify">
              <span className="workflow-footer-icon">‚ú®</span>
              Prettify
            </button>
            <button className="workflow-footer-btn" title="Inspect">
              <span className="workflow-footer-icon">üîç</span>
              Inspect
            </button>
          </div>
        </footer>
      </main>
    </div>
  );
}
